@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  overflow: scroll;
}

.container {
  @apply max-w-[1440px] m-auto px-8 md:px-20 flex;
}

#tsparticles {
  @apply h-full w-screen absolute -z-10;
}

* {
  margin: 0;
  padding: 0;
}

body *::-webkit-scrollbar {
  display: none;
}

.HTML {
  @apply text-orange-400;
}

.CSS {
  @apply text-blue-500;
}

.Javascript {
  @apply text-yellow-300;
}

.Laravel {
  @apply text-red-500;
}

.Flutter {
  @apply text-blue-600;
}

.React {
  @apply text-cyan-500;
}

.SASS {
  @apply text-pink-300;
}

.Tailwind {
  @apply text-green-300;
}

.move1 {
  opacity: 0;
  transform: translateY(100%) scale(0.5);
}

.move2 {
  opacity: 0.4;
  transform: translateX(50%) scale(0.7);
  filter: blur(10px);
}

.move3 {
  opacity: 1;
  pointer-events: auto;
  transform: translateX(0%) scale(1);
  z-index: 10;
}

.move4 {
  opacity: 0.4;
  transform: translateX(-50%) scale(0.7);
  filter: blur(10px);
}

.move5 {
  opacity: 0;
  transform: translateY(-100%) scale(0.5);
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateY(100%) scale(0.5);
  }

  5%,
  20% {
    opacity: 0.4;
    transform: translateY(100%) scale(0.7);
  }

  25%,
  40% {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0%) scale(1);
  }

  45%,
  60% {
    opacity: 0.4;
    transform: translateY(-100%) scale(0.7);
  }

  65%,
  100% {
    opacity: 0;
    transform: translateY(-100%) scale(0.5);
  }
}

